body {
  font-family: sans-serif;
}

/* Make content area fill the entire browser window */
html,
.fullscreen {
  display: flex;
  height: 100%;
  margin: 0;
  padding: 0;
  width: 100%;
}

/* Center the content in the browser window */
.container {
  margin: auto;
  text-align: center;
}

.title {
  font-size: 5rem;
  color: rgb(45, 255, 55);
}

.app-button {
  /* display: none; */
}

@media (display-mode: standalone) {
  .app-button {
    display: block;
  }
}
